<template>
    <div class="reset-container">
        <el-form autoComplete="on" label-position="left"
                 label-width="0px"
                 class="card-box reset-form">
            <div>
                <router-link to="/basic-credit" class="back-icon">
                    首页<i class="el-icon-arrow-right"></i>信用分基础设置<i class="el-icon-arrow-right"></i>修改
                </router-link>
            </div>
            <el-form-item prop="email">
                <span class="title">用户单日上限总分</span>
                <span class="choose">
                 <el-radio v-model="scoreMax" label="2">无上限</el-radio>                          
                 </span>
                 <span class="choose">
                <el-radio v-model="scoreMax" label="1">{{emptyctxt}}</el-radio><el-input name="email" type="number" :disabled="scoreMax==2" v-model="scoreMaxInput" placeholder="单日上限总分" @input="validScore"></el-input>
                 </span>
            </el-form-item>
            <!--<el-form-item prop="code">
                <span class="title">任务上限总分</span>
                <span class="choose">
                <el-radio v-model="missionMax" label="2">无上限</el-radio>
                </span>
                <span class="choose">
                <el-radio v-model="missionMax" label="1">{{emptyctxt}}</el-radio><el-input name="code" type="number" :disabled="missionMax==2" v-model="missionMaxInput" placeholder="任务上限总分" @input="validMission"></el-input>
                </span>
            </el-form-item>-->
            <el-form-item style="width:100%;">
                <el-button type="primary" style="width:80px" @click="updatedMax">
                    修改
                </el-button>
            </el-form-item>
        </el-form>
    </div>
</template>
<script>
    import {
        updateBasicCredit
    } from 'api/basicCredit';

    export default {
        name: 'reset',
        data() {
            return {
                emptyctxt: "",
                missionMax: '2',
                missionMaxInput: 0,
                scoreMaxInput: 0,
                scoreMax: '2'
            }
        },
        methods: {
            updatedMax() {
                var _vue = this;
                // let missionMax = this.missionMax == 2 ? "" : this.missionMaxInput;
                let ScoreMax = this.scoreMax == 2 ? "-1" : this.scoreMaxInput;
                let id = this.$route.query.id ? this.$route.query.id : "";
                updateBasicCredit(ScoreMax, id).then(response => {
                    if (response.success) {
                        _vue.open();
                    } else {
                        _vue.open(response.data);
                    }
                    resolve();
                }).catch(error => {
                    // reject(error);
                });
            },
            validMission() {
                if (this.missionMaxInput == "" || this.missionMaxInput < 0) {
                    var _obj = this;
                    this.$nextTick(function() {
                        _obj.missionMaxInput = 0;
                    });
                }
                if ((this.missionMaxInput + "").length > 1 && (this.missionMaxInput + "").startsWith("0")) {
                    var _obj = this;
                    this.$nextTick(function() {
                        _obj.missionMaxInput = 0;
                    });
                }
                var _obj = this;
                this.$nextTick(function() {
                    _obj.missionMaxInput = _obj.toDecimal2(_obj.missionMaxInput);
                });
            },
            open(info) {
                var _vue = this;
                this.$alert(info ? info : '操作成功', '提示信息', {
                    confirmButtonText: '确定',
                    callback: action => {
                        _vue.$router.push('/basic-credit');
                    }
                });
            },
            validScore() {
                if (this.scoreMaxInput == "" || this.scoreMaxInput < 0) {
                    var _obj = this;
                    this.$nextTick(function() {
                        _obj.scoreMaxInput = 0;
                    });
                }
                if ((this.scoreMaxInput + "").length > 1 && (this.scoreMaxInput + "").startsWith("0") && (this.scoreMaxInput + "").indexOf(".") != 1) {
                    var _obj = this;
                    this.$nextTick(function() {
                        _obj.scoreMaxInput = 0;
                    });
                }
                     var _obj = this;
                this.$nextTick(function() {
                    _obj.scoreMaxInput = _obj.toDecimal2(_obj.scoreMaxInput);
                });
            },
            togglePasswordType() {
                if (this.passwordType === 'text') {
                    this.passwordType = 'password'
                } else {
                    this.passwordType = 'text'
                }
            },
            toDecimal2(x) {
                var f = parseFloat(x);
                if (isNaN(f)) {
                    return false;
                }
                var f = Math.round(x * 100) / 100;
                var s = f.toString();
                var rs = s.indexOf('.');
                if (rs < 0) {
                    rs = s.length;
                    s += '.';
                }
                while (s.length <= rs + 2) {
                    s += '0';
                }
                return s;
            }
        }
    }
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
    @import "src/styles/mixin.scss";
    .reset-container {
        .el-form-item {
            float: left;
            margin-top: 20px;
            .title {
                float: left;
            }
            .choose {
                width: 60%;
                float: right;
                .el-radio {
                    float: left;
                }
                .el-input {
                    display: inline;
                    float: left;
                    width: 70%;
                    input {
                        float: left;
                        width: 100%;
                    }
                }
            }
        }
        input:-webkit-autofill {
            -webkit-box-shadow: 0 0 0px 1000px #293444 inset !important;
            -webkit-text-fill-color: #3E3E3E !important;
        }
        @include relative;
        height: 100vh;
        background-color: #324057;
        .back-icon {
            float: left;
            margin-top: 5px;
        }
        .el-icon-information {
            position: absolute;
            right: -18px;
            top: 10px;
        }
        .reset-form {
            position: absolute;
            left: 0;
            right: 0;
            width: 350px;
            padding: 35px 35px 15px 35px;
            margin: 120px auto;
        }
        .card-box {
            padding: 20px;
            box-shadow: 0 0px 8px 0 rgba(0, 0, 0, 0.06), 0 1px 0px 0 rgba(0, 0, 0, 0.02);
            -webkit-border-radius: 5px;
            border-radius: 5px;
            -moz-border-radius: 5px;
            background-clip: padding-box;
            margin-bottom: 20px;
            background-color: #F9FAFC;
            width: 400px;
            border: 2px solid #8492A6;
        }
        .title {
            margin: 0px auto 40px auto;
            text-align: center;
            color: #505458;
        }
    }
</style>


